import React, { Component } from "react";
import Helmet from "react-helmet";
import config from "../../../data/SiteConfig";

class SEO extends Component {
  render() {
    const { postNode, postPath, postSEO, addiitionalJsonLD } = this.props;
    let title;
    let description;
    let image;
    let postURL;
    let keywords;
    if (postSEO) {
      const postMeta = postNode.frontmatter;
      title = postMeta.title;
      description = postMeta.description
        ? postMeta.description
        : postNode.excerpt;
      image = postMeta.cover;
      postURL = config.siteUrl + config.pathPrefix + postPath;
      keywords = postNode.frontmatter.tags.join(",");
    } else {
      title = config.siteTitle;
      description = config.siteDescription;
      image = config.siteLogo;
    }
    const realPrefix = config.pathPrefix === "/" ? "" : config.pathPrefix;
    image = config.siteUrl + realPrefix + image;
    const blogURL = config.siteUrl + config.pathPrefix;
    const schemaOrgJSONLD = [
      {
        "@context": "http://schema.org",
        "@type": "WebSite",
        url: blogURL,
        name: title,
        alternateName: config.siteTitleAlt ? config.siteTitleAlt : ""
      }
    ];
    return (
      <React.Fragment>
        <Helmet>
          {/* General tags */}
          <meta name="description" content={description} />
          <meta name="image" content={image} />
          {keywords ? <meta property="keywords" content={keywords} /> : null}
          <meta name="author" content={config.userName} />

          {/* Schema.org tags */}
          <script type="application/ld+json">
            {JSON.stringify([...schemaOrgJSONLD, ...addiitionalJsonLD])}
          </script>

          {/* OpenGraph tags */}
          <meta property="og:url" content={postSEO ? postURL : blogURL} />
          {postSEO ? <meta property="og:type" content="article" /> : null}
          <meta property="og:title" content={title} />
          <meta property="og:description" content={description} />
          <meta property="og:image" content={image} />
          <meta
            property="fb:app_id"
            content={config.siteFBAppID ? config.siteFBAppID : ""}
          />

          {/* Twitter Card tags */}
          <meta name="twitter:card" content="summary_large_image" />
          <meta
            name="twitter:creator"
            content={config.userTwitter ? config.userTwitter : ""}
          />
          <meta name="twitter:title" content={title} />
          <meta name="twitter:description" content={description} />
          <meta name="twitter:image" content={image} />
        </Helmet>
      </React.Fragment>
    );
  }
}

export default SEO;
